/**
 * @file
 * Nav Primary.
 */

.primary-nav__menu {
  --color-background: var(--theme-white);
  --color-text-neutral-loud: var(--text-action);
  --color-text-neutral-soft: var(--text-action);

  --wide-font-size: 12px;
  --wide-font-size-submenu: 14px;
  --color-highlight: var(--theme-secondary);
  --color-icon: var(--text-action);

  --narrow-font-size: 18px;
  --narrow-font-weight: 600;
  --narrow-font-size-submenu: 14px;
  --narrow-toggle-button-width: calc(4 * var(--spacing));

  --spacing: var(--spacing-xs);

  list-style: none;
}

.primary-nav__menu-item {
  margin-block-end: calc(1.5 * var(--spacing));

  &:last-child {
    margin-block-end: 0;
  }

  &.primary-nav__menu-item--has-children {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    & .primary-nav__menu-link--link,
    & .primary-nav__menu-link--nolink {
      /* Ensure that long text doesn't make the mobile expand button wrap. */
      flex-basis: calc(100% - var(--sp3));
    }
  }
}

.primary-nav__menu-link {
  flex-grow: 1;
  text-decoration: none;
  color: var(--color-text-neutral-loud);
  font-size: var(--narrow-font-size);
  font-weight: var(--narrow-font-weight);
  line-height: 1.4;

  &:hover {
    color: var(--color-text-neutral-loud);
  }

  &:focus-visible {
    outline: auto 2px currentColor;
    outline-offset: 2px;
  }
}

.primary-nav__menu-link--nolink {
  padding-block: var(--spacing);
  padding-inline: 0;
  color: var(--color-text-neutral-soft);
  font-weight: normal;
}

.primary-nav__menu-link--button {
  position: relative;
  padding: 0;
  cursor: pointer;
  text-align: start;
  border: 0;
  background: transparent;

  /* Plus icon for mobile navigation. */
  &.primary-nav__menu-link--has-children {
    padding-inline-end: var(--narrow-toggle-button-width); /* Ensure text does not overlap icon. */

    &::before,
    &::after {
      position: absolute;
      inset-inline-end: 9px;
      inset-block-start: 11px; /* Visually align button with menu link text. */
      width: 15px;
      height: 0;
      content: "";
      border-top: solid 3px var(--color-icon);
    }

    &::after {
      transition: opacity 0.2s;
      transform: rotate(90deg);
    }

    &[aria-expanded="true"]::after {
      opacity: 0;
    }
  }
}

/*
  Top level specific styles.
*/
.primary-nav__menu--level-1 {
  margin: 0;
  padding: 0;
}

.primary-nav__menu-link--level-1 {
  position: relative;
  display: flex;
}

/*
  Secondary menu specific styles.
*/
.primary-nav__menu--level-2 {
  visibility: hidden;
  overflow: hidden;
  flex-basis: 100%;
  max-height: 0;
  margin-block: 0;
  margin-inline-start: calc(-1 * var(--container-padding));
  padding-inline-start: calc(2 * var(--spacing));
  transition: opacity 0.2s, visibility 0.2s, max-height 0.2s;
  opacity: 0;
  border-inline-start: solid var(--spacing) var(--color-highlight);

  &.is-active-menu-parent {
    visibility: visible;
    max-height: none;
    margin-block-start: calc(1.5 * var(--spacing));
    opacity: 1;
  }
}

/*
 * We officially support nested tertiary submenus, but this
 * ensures that it doesn't break all the way.
 */
.primary-nav__menu--level-2 .primary-nav__menu-item--has-children {
  display: block;
}

.primary-nav__menu-link--level-2 {
  font-size: var(--wide-font-size-submenu);
  font-weight: normal;
  line-height: 1.5;
}

html:not(.js) {
  & .primary-nav__menu--level-2 {
    visibility: visible;
    max-height: none;
    opacity: 1;
  }
}
